.page{
    width: 100%;
    //flex-wrap: wrap;
}

.btn{
    font-size: 0.875rem;
    padding: 0.570rem 1.812rem;

    background: #000;
    color: #fff;
    opacity: 0;

    border-radius: 5px;
}

.page-start{
    display: flex;

    align-items: center;
    flex-direction: column;

    min-height: 100vh;
    
    background: radial-gradient(#FFF, #f5f5f5);
    
    .btn{
        //margin: 158px 0 -158px 0;
        margin-top: 158px;
        margin-bottom: 125px;
    }

    &__content{
        display: flex;
        flex-direction: column;
        align-items: center;

        flex-grow: 1;

        .main{
            text-transform: uppercase;
            font-weight: bold;
            font-size: 4.5rem;

            margin-bottom: 4px;
            flex-grow: 1.1;

            display: flex;
            align-items: flex-end;

            span:not(.iil):not(.mov){
                height: 100px;

                animation: fadeIn 0.2s ease-in;
            }
        }

        .sub{
            font-size: 0.875rem;
            font-weight: 500;

            margin-top: -13px;

            opacity: 0;
            animation: fadeInNonMargin 0.2s 3s ease-in forwards;
        }

        .sub-2{
            margin-top: 3px;
            font-weight: 100;
            color: #ababab;
        }

        .go{
            flex-grow: 0;
            margin-top: 16px;
            font-size: 1.8rem;
            transform: rotate(180deg);

            transition: all 0.2s ease-in-out;
            cursor: pointer;
            
            &:hover{
                margin-bottom: -4px;
                margin-top: 20px;
            }
        }
    }

    &__links{
        flex-grow: 1;
        display: flex;
        flex-direction: row;
        align-items: flex-end;
        // padding-bottom: 50px;
        .link{
            width: 40px;
            height: 40px;
            margin: 0 5px;
            opacity: 0;

            cursor: pointer;

            margin-bottom: 40px;

            &.github{
                background: url('/img/links/github.png');
            }

            &.vk{
                background: url('/img/links/vk.png');
            }

            &:nth-child(1){
                animation: fadeInNonMargin 0.2s 3.1s ease-in forwards;
            }

            &:nth-child(2){
                animation: fadeInNonMargin 0.2s 3.5s ease-in forwards;
            }
        }
    }
}

.page-about{
    display: flex;
    justify-content: space-between;
    background-color: #fff;
    align-items: center;
    flex-direction: column;

    position: relative;

    min-height: 955px;
    height: 100vh;
    max-height: 1200px;
    overflow: hidden;
    
    &__content{
        display: flex;

        align-items: center;
        flex-direction: column;

        .name{
            margin-top: 55px;
            font-size: 1.5rem;
            font-weight: 500;
            text-transform: capitalize;
            color: #323232;
        }

        .phrase{
            font-size: 2.5rem;
            font-weight: bold;
            margin-top: 12px;
        }

        .btn-summary{
            font-size: 1.12rem;
            color: #4D3EF9;
            margin-top: 12px;
            margin-bottom: 50px;

            text-decoration: none;

            &::after{
                
            }
        }
    }
}

.photo-wrapper{
    display: flex;
}

.photo-item{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;

    cursor: pointer;

    &:hover{
        span{
            //margin-bottom: 10px;
        }
    }

    span{
        transition: margin-bottom 0.2s ease-in-out;
        display: inline-block;
        font-size: 2.25rem;
        font-weight: bold;
    }

    &__img{
        margin-top: 30px;
        width: 500px;
        height: 500px;

        background-size: contain;
        background-repeat: no-repeat;
        background-position: center bottom;
    }

    &.evil{
        .photo-item__img{
            background-image: url('/img/faces/evil.png');
        }

        span{
            color: rgb(255, 59, 48);
        }
    }
    
    &.happy{
        .photo-item__img{
            background-image: url('/img/faces/happy.png');
        }

        span{
            color: rgb(10, 132, 255);
        }
    }

    &.normal{
        margin: 0 -150px;
        z-index: 100;
        .photo-item__img{
            height: 450px;
            background-image: url('/img/faces/normal.png');
        }

        span{
            color: rgb(0, 0, 0);
        }
    }
}

.mov{
    width: 165px;
    overflow: hidden;
    display: inline-flex;
    color: #919191;
}

.iil{
    width: 165px;
    overflow: hidden;
    display: inline-flex;
    color: #919191;
}

footer{
    background-color: #F8F8F8;
    height: 155px;

    display: flex;
    align-items: center;
    justify-content: center;

    .footer-wrapper{
        width: 1100px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .name{
        color: #525252;
        font-size: 1.12rem;
        font-weight: bold;
        margin-bottom: 11px;
    }

    .label{
        color: #7b7b7b;
        font-size: 1rem;
        margin-bottom: 8px;
        display: block;
        text-decoration: none;
        font-style: normal; 
    }
}



