@keyframes fadeIn{
    from {
        opacity: 0;
        margin-top: 10px;
        margin-bottom: -10px;
    }
    to {
        opacity: 1;
        margin-top: 0;
        margin-bottom: 0;
    }
}

@keyframes fadeOut{
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

@keyframes fadeInNonMargin{
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}


@keyframes slideLeft{
    0%{
        opacity: 1;
        width: 165px;
    }

    40%{
        opacity: 0;
        width: 165px;
    }

    100%{
        opacity: 0;
        width: 0px;
    }
}

.up{
    cursor: pointer;
    position: absolute;
    transition: transform 0.2s linear;
    bottom: 80px;
    z-index: 1010;
    font-size: 1.5rem;
    &.visible{
        transform: rotate(180deg);
    }
}

.text{
    max-width: 500px;
    text-align: center;
    font-size: 1.2rem;
    flex-grow: 1;
    color: #86868b;
}